"use client"

import { useState, useEffect } from "react"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Progress } from "@/components/ui/progress"
import { useParams, useRouter } from "next/navigation"

interface RideDetails {
  id: string
  pickup: string
  dropoff: string
  status: 'pending' | 'accepted' | 'driver-arriving' | 'in-progress' | 'completed' | 'cancelled'
  driver: {
    name: string
    phone: string
    rating: number
    vehicle: {
      make: string
      model: string
      color: string
      plate: string
    }
  } | null
  fare: number
  estimatedArrival: string
  createdAt: string
  completedAt?: string
}

export default function TrackRidePage() {
  const params = useParams()
  const router = useRouter()
  const rideId = params.rideId as string
  
  const [rideDetails, setRideDetails] = useState<RideDetails | null>(null)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)

  useEffect(() => {
    const fetchRideDetails = async () => {
      try {
        setLoading(true)
        
        // Simulate API call
        await new Promise(resolve => setTimeout(resolve, 1000))
        
        // Mock ride data
        const mockRide: RideDetails = {
          id: rideId,
          pickup: "Downtown Plaza, 123 Main St",
          dropoff: "Airport Terminal 1, Gate A",
          status: "driver-arriving",
          driver: {
            name: "John Smith",
            phone: "+1 (555) 123-4567",
            rating: 4.8,
            vehicle: {
              make: "Toyota",
              model: "Camry",
              color: "Silver",
              plate: "ABC-123"
            }
          },
          fare: 25.50,
          estimatedArrival: new Date(Date.now() + 8 * 60000).toISOString(), // 8 minutes from now
          createdAt: new Date(Date.now() - 5 * 60000).toISOString() // 5 minutes ago
        }
        
        setRideDetails(mockRide)
      } catch (err) {
        setError("Failed to load ride details")
      } finally {
        setLoading(false)
      }
    }

    fetchRideDetails()

    // Set up polling for real-time updates
    const interval = setInterval(fetchRideDetails, 10000) // Poll every 10 seconds

    return () => clearInterval(interval)
  }, [rideId])

  const getStatusInfo = (status: string) => {
    switch (status) {
      case 'pending':
        return { 
          text: 'Finding Driver', 
          color: 'bg-yellow-100 text-yellow-800',
          progress: 20,
          description: 'We are looking for a driver near you'
        }
      case 'accepted':
        return { 
          text: 'Driver Assigned', 
          color: 'bg-blue-100 text-blue-800',
          progress: 40,
          description: 'Your driver is preparing to pick you up'
        }
      case 'driver-arriving':
        return { 
          text: 'Driver Arriving', 
          color: 'bg-orange-100 text-orange-800',
          progress: 60,
          description: 'Your driver is on the way to pick you up'
        }
      case 'in-progress':
        return { 
          text: 'In Progress', 
          color: 'bg-green-100 text-green-800',
          progress: 80,
          description: 'You are on your way to the destination'
        }
      case 'completed':
        return { 
          text: 'Completed', 
          color: 'bg-gray-100 text-gray-800',
          progress: 100,
          description: 'Your ride has been completed'
        }
      case 'cancelled':
        return { 
          text: 'Cancelled', 
          color: 'bg-red-100 text-red-800',
          progress: 0,
          description: 'This ride has been cancelled'
        }
      default:
        return { 
          text: 'Unknown', 
          color: 'bg-gray-100 text-gray-800',
          progress: 0,
          description: 'Status unknown'
        }
    }
  }

  const formatTime = (dateString: string) => {
    const date = new Date(dateString)
    return date.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })
  }

  const getEstimatedArrival = () => {
    if (!rideDetails?.estimatedArrival) return null
    const now = new Date()
    const arrival = new Date(rideDetails.estimatedArrival)
    const diffMinutes = Math.max(0, Math.ceil((arrival.getTime() - now.getTime()) / 60000))
    return diffMinutes
  }

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <div className="w-8 h-8 border-4 border-black border-t-transparent rounded-full animate-spin mx-auto mb-4"></div>
          <p className="text-gray-600">Loading ride details...</p>
        </div>
      </div>
    )
  }

  if (error || !rideDetails) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <Card className="max-w-md mx-auto">
          <CardContent className="text-center py-8">
            <div className="w-16 h-16 bg-red-100 rounded-full mx-auto mb-4 flex items-center justify-center">
              <span className="text-red-600 text-2xl">!</span>
            </div>
            <h2 className="text-xl font-semibold mb-2">Error Loading Ride</h2>
            <p className="text-gray-600 mb-4">{error || "Ride not found"}</p>
            <Button onClick={() => router.push('/customer/dashboard')} className="bg-black hover:bg-gray-800 text-white">
              Back to Dashboard
            </Button>
          </CardContent>
        </Card>
      </div>
    )
  }

  const statusInfo = getStatusInfo(rideDetails.status)
  const estimatedMinutes = getEstimatedArrival()

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Header */}
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-black mb-2">Track Your Ride</h1>
          <p className="text-gray-600">Ride #{rideDetails.id.slice(-6)}</p>
        </div>

        <div className="grid lg:grid-cols-3 gap-8">
          {/* Main Tracking Info */}
          <div className="lg:col-span-2 space-y-6">
            {/* Status Card */}
            <Card>
              <CardHeader>
                <div className="flex justify-between items-center">
                  <CardTitle>Ride Status</CardTitle>
                  <Badge className={statusInfo.color}>
                    {statusInfo.text}
                  </Badge>
                </div>
                <CardDescription>{statusInfo.description}</CardDescription>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  <Progress value={statusInfo.progress} className="w-full" />
                  
                  {estimatedMinutes !== null && estimatedMinutes > 0 && (
                    <div className="text-center">
                      <p className="text-2xl font-bold text-black">{estimatedMinutes} min</p>
                      <p className="text-gray-600">Estimated arrival</p>
                    </div>
                  )}
                </div>
              </CardContent>
            </Card>

            {/* Trip Details */}
            <Card>
              <CardHeader>
                <CardTitle>Trip Details</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  <div>
                    <p className="text-sm text-gray-600 mb-1">Pickup Location</p>
                    <p className="font-medium">{rideDetails.pickup}</p>
                  </div>
                  <div className="border-l-2 border-gray-200 ml-2 pl-4 py-2">
                    <div className="w-2 h-2 bg-gray-400 rounded-full -ml-5 mb-2"></div>
                    <div className="w-2 h-2 bg-gray-400 rounded-full -ml-5 mb-2"></div>
                    <div className="w-2 h-2 bg-gray-400 rounded-full -ml-5"></div>
                  </div>
                  <div>
                    <p className="text-sm text-gray-600 mb-1">Drop-off Location</p>
                    <p className="font-medium">{rideDetails.dropoff}</p>
                  </div>
                  <div className="flex justify-between items-center pt-4 border-t">
                    <span className="text-gray-600">Total Fare</span>
                    <span className="text-xl font-bold">${rideDetails.fare.toFixed(2)}</span>
                  </div>
                </div>
              </CardContent>
            </Card>

            {/* Map Placeholder */}
            <Card>
              <CardHeader>
                <CardTitle>Live Map</CardTitle>
                <CardDescription>Track your driver's location in real-time</CardDescription>
              </CardHeader>
              <CardContent>
                <div className="bg-gray-100 rounded-lg h-64 flex items-center justify-center">
                  <div className="text-center">
                    <div className="w-16 h-16 bg-gray-300 rounded-full mx-auto mb-4 flex items-center justify-center">
                      <span className="text-gray-600 text-2xl">üìç</span>
                    </div>
                    <p className="text-gray-600">Interactive map will be displayed here</p>
                    <p className="text-sm text-gray-500 mt-1">Showing driver location and route</p>
                  </div>
                </div>
              </CardContent>
            </Card>
          </div>

          {/* Driver Info Sidebar */}
          <div>
            {rideDetails.driver ? (
              <Card className="sticky top-8">
                <CardHeader>
                  <CardTitle>Your Driver</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="space-y-4">
                    {/* Driver Info */}
                    <div className="text-center">
                      <div className="w-16 h-16 bg-gray-200 rounded-full mx-auto mb-3 flex items-center justify-center">
                        <span className="text-gray-600 text-xl font-semibold">
                          {rideDetails.driver.name.split(' ').map(n => n[0]).join('')}
                        </span>
                      </div>
                      <h3 className="font-semibold text-lg">{rideDetails.driver.name}</h3>
                      <div className="flex items-center justify-center mt-1">
                        <span className="text-yellow-500 mr-1">‚òÖ</span>
                        <span className="text-sm text-gray-600">{rideDetails.driver.rating}</span>
                      </div>
                    </div>

                    {/* Vehicle Info */}
                    <div className="border-t pt-4">
                      <h4 className="font-medium mb-2">Vehicle Details</h4>
                      <div className="space-y-1 text-sm">
                        <p><span className="text-gray-600">Car:</span> {rideDetails.driver.vehicle.color} {rideDetails.driver.vehicle.make} {rideDetails.driver.vehicle.model}</p>
                        <p><span className="text-gray-600">Plate:</span> {rideDetails.driver.vehicle.plate}</p>
                      </div>
                    </div>

                    {/* Contact Button */}
                    <div className="border-t pt-4">
                      <Button 
                        variant="outline" 
                        className="w-full border-black text-black hover:bg-black hover:text-white"
                        onClick={() => window.open(`tel:${rideDetails.driver?.phone}`)}
                      >
                        Call Driver
                      </Button>
                    </div>

                    {/* Cancel Ride (only if not in progress) */}
                    {rideDetails.status !== 'in-progress' && rideDetails.status !== 'completed' && (
                      <div className="border-t pt-4">
                        <Button 
                          variant="outline" 
                          className="w-full border-red-500 text-red-500 hover:bg-red-500 hover:text-white"
                          onClick={() => {
                            if (confirm('Are you sure you want to cancel this ride?')) {
                              // Handle cancellation
                              router.push('/customer/dashboard')
                            }
                          }}
                        >
                          Cancel Ride
                        </Button>
                      </div>
                    )}
                  </div>
                </CardContent>
              </Card>
            ) : (
              <Card>
                <CardContent className="text-center py-8">
                  <div className="w-16 h-16 bg-gray-100 rounded-full mx-auto mb-4 flex items-center justify-center">
                    <span className="text-gray-400 text-2xl">üë§</span>
                  </div>
                  <p className="text-gray-600">Searching for driver...</p>
                </CardContent>
              </Card>
            )}
          </div>
        </div>
      </div>
    </div>
  )
}
